aside {
	background-color: $primary-color;
	color: white;
	grid-area: sidebar;
	height: 100%;
	line-height: 40px;
	padding: 30px;
	position: relative;

	h1 {
		font-size: 4.375rem;
		font-variant-caps: normal;
		font-weight: 600;
		letter-spacing: -0.125rem;
		line-height: 4.0625rem;
		text-align: center;
	}

	.picture {
		padding: 30px 0;
		img {
			border-radius: 50%;
			width: 100%;
		}
	}

	.personal-information {
		border-top: 5px solid white;
		padding: 20px 0;

		h2 {
			font-size: 1.5rem;
			font-weight: 600;
			margin-bottom: 20px;
			text-transform: uppercase;
		}

		p {
			font-size: 1.125rem;
			font-weight: 500;
		}

		.social-item {
			margin-bottom: 5px;
		}

		.icon {
			font-size: 1.875rem;
			margin-right: 7px;
			margin-top: 5px;
		}

		a {
			color: white;
			display: inline-block;
			font-size: 1.125rem;
			font-weight: 500;

			&:hover {
				text-decoration: none;
			}
		}
	}

	.triangle {
		bottom: 0;
		height: 200px;
		left: 0;
		overflow: hidden;
		padding-bottom: 10%;
		position: absolute;
		width: 100%;
	}
	.triangle:after {
		content: '';
		display: block;
		width: 0;
		height: 0;
		border-right: 1100px solid transparent;
		border-bottom: 500px solid #3f606e;
	}
}
