@media (max-width: 1200px) {
	html {
		font-size: 14px;
	}

	aside {
		.triangle:after {
			border-right: 900px solid transparent;
			border-bottom: 500px solid #3f606e;
		}
	}
}

@media (max-width: 992px) {
	html {
		font-size: 14px;
	}

	.container {
		grid-template-columns: 1fr;
		grid-template-areas:
			'sidebar'
			'main';

		main {
			padding: 30px 10px;
		}

		aside {
			align-items: center;
			display: flex;
			justify-content: space-evenly;

			h1 {
				font-size: 2.5rem;
				line-height: 2.1875rem;
				padding: 0 30px;
			}

			.picture {
				padding: 30px;
			}

			.personal-information {
				border-top: none;

				h2 {
					font-size: 1.25rem;
					margin-bottom: 10px;
				}

				p,
				a {
					font-size: 1rem;
				}

				.icon {
					font-size: 1.5rem;
				}
			}

			.triangle {
				display: none;
			}
		}
	}
}

@media (max-width: 768px) {
	html {
		font-size: 16px;
	}
	.container {
		main {
			.skills {
				.photoshop::after {
					content: 'Photoshop';
				}
				.after-effects {
					width: 40%;

					&::after {
						content: 'After Effects';
					}
				}
				.premiere {
					width: 50%;

					&::after {
						content: 'Premiere Pro';
					}
				}
				.office::after {
					content: 'Office';
				}

				.photoshop::after,
				.after-effects::after,
				.premiere::after,
				.office::after {
					margin: 0 5px;
				}
			}
		}
		aside {
			flex-direction: column;

			.picture {
				padding: 30px;
			}

			.personal-information {
				border-top: 5px solid white;
			}
		}
	}
}

@media (max-width: 576px) {
	html {
		font-size: 14px;
	}

	.container {
		main {
			.photoshop::after,
			.after-effects::after,
			.premiere::after,
			.office::after {
				font-size: 0.8rem;
			}
		}
	}
}
